@import "./default/default.scss";

.tabs-block {
  .tab-bar {
    display: flex;
    justify-content: space-around;
    max-width: 1260px;
    margin-left: auto;
    margin-right: auto;
    .tab-bar-item.tab-button {
      display: flex;
      align-items: center;
      gap: 15px;
      background: none;
      border: none;
      word-spacing: 0.05em;
      letter-spacing: 0.01em;
      padding-bottom: 5px;
      border-bottom: 3px solid rgba($color: #000000, $alpha: 0);
      &:hover {
        border-color: $darkGreen;
        color: $darkGreen;
        .icon-lorry {
          color: $darkGreen;
        }
        .icon-package {
          color: $darkGreen;
        }
        .icon-wallet-card {
          color: $darkGreen;
        }
      }
      &.active {
        border-color: $darkGreen;
        .icon-lorry {
          color: $darkGreen;
        }
        .icon-package {
          color: $darkGreen;
        }
        .icon-wallet-card {
          color: $darkGreen;
        }
      }
      @include rale-800-20;
      
      &:hover {
      }
      &:active {
      }
      &:focus {
      }
    }
  }
  hr {
    margin: 0;
  }
  .tab-content-block {
    padding-top: 52px;
    .tabs-block__text-block-with-img {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      .tabs-block__text-block {
        min-width: 500px;
      }
      .tabs-block__img-block {
        min-height: 100%;
      }
      @media (max-width: 750px) {
        flex-direction: column-reverse;
      }
      .tabs-block__text-blocks {
        margin-top: 19px;
        display: flex;
        flex-direction: column;
        gap: 37px;
        .tabs-block-icon-text-block {
          
          .icon-bank {
          }
          .icon-bank,
          .icon-bank-card,
          .icon-wallet-money {
            position: relative;
            width: 48px;
            height: 48px;
            display: flex;
            justify-content: center;
            align-items: center;
            &::after {
              content: "";
              position: absolute;
              width: 48px;
              height: 48px;
              border-radius: 50%;
              background-color: rgba($color: #93b474, $alpha: 0.15);
              top: 0;
              left: 0;
            }
            &::before {
              position: relative;
              display: flex;
              justify-content: center;
              align-items: center;
              width: 36px;
              height: 36px;
              border-radius: 50%;
              background-color: $darkGreen;
              color: $white;
            }
          }
        }
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
