.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.tabs-block .tab-bar {
  display: flex;
  justify-content: space-around;
  max-width: 1260px;
  margin-left: auto;
  margin-right: auto;
}
.tabs-block .tab-bar .tab-bar-item.tab-button {
  display: flex;
  align-items: center;
  gap: 15px;
  background: none;
  border: none;
  word-spacing: 0.05em;
  letter-spacing: 0.01em;
  padding-bottom: 5px;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  font-size: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
}
.tabs-block .tab-bar .tab-bar-item.tab-button:hover {
  border-color: #337d5a;
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button:hover .icon-lorry {
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button:hover .icon-package {
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button:hover .icon-wallet-card {
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button.active {
  border-color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button.active .icon-lorry {
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button.active .icon-package {
  color: #337d5a;
}
.tabs-block .tab-bar .tab-bar-item.tab-button.active .icon-wallet-card {
  color: #337d5a;
}
.tabs-block hr {
  margin: 0;
}
.tabs-block .tab-content-block {
  padding-top: 52px;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-block {
  min-width: 500px;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__img-block {
  min-height: 100%;
}
@media (max-width: 750px) {
  .tabs-block .tab-content-block .tabs-block__text-block-with-img {
    flex-direction: column-reverse;
  }
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks {
  margin-top: 19px;
  display: flex;
  flex-direction: column;
  gap: 37px;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank-card,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-wallet-money {
  position: relative;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank::after,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank-card::after,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-wallet-money::after {
  content: "";
  position: absolute;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(147, 180, 116, 0.15);
  top: 0;
  left: 0;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank::before,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-bank-card::before,
.tabs-block .tab-content-block .tabs-block__text-block-with-img .tabs-block__text-blocks .tabs-block-icon-text-block .icon-wallet-money::before {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #337d5a;
  color: #ffffff;
}
.tabs-block .tab-content-block .tabs-block__text-block-with-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}